:root {
    --primary: #888383;
    --light: #F3F3F3;
    --grey: #8A8A8B;
    --dark: #000000;
    --Gbeige: #727170;
    // --transition: linear-gradient(to top, #a8edea 0%, #fed6e3 100%);
    --transition: #F3F3F3;
}
  
*,
*:after,
*:before{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
  overflow: hidden;
  width: 100%;
  //* RESPONSIVE
  font-size: calc(100vw / 1920 * 10);

  @include media('<tablet'){
    overflow: auto;
  }

  @include media('<phone') {
    overflow: auto;
    font-size: calc(100vw / 750 * 10);
  }
}

