// MENU SPLIT
.btn {
  display: flex;
  justify-content: center;
  padding: 12px 22px;
  // color: rgb(0, 0, 0);
  text-decoration: none;
  text-transform: uppercase;
  cursor: pointer;
}

.btn-line-hover {
  position: relative;
  transition: letter-spacing 500ms ease-in-out;
}

.btn-line-hover:hover,
.btn-line-hover:active {
  letter-spacing: 5px;
}


